<template>
    <div>
        <el-button type="text" @click="dialogTableVisible = true">选择优惠券</el-button>
        <el-dialog title="选择模板" :visible.sync="dialogTableVisible" width="80%" :modal-append-to-body="false">
            <div class="wyu-block" v-loading="loading" >
                <el-form size="mini" v-model="searchForm" class="search-form" :inline="true">
                    <el-form-item label="优惠券名称">
                        <el-input v-model="searchForm.keyword" placeholder="输入关键字"></el-input>
                    </el-form-item>

                    <el-form-item label="领取时间">
                        <date-between  v-model="searchForm.send_date"></date-between>
                    </el-form-item>

                    <el-form-item label="有效时间">
                        <date-between  v-model="searchForm.expire_date"></date-between>
                    </el-form-item>

                    <div style="padding-left:80px;">
                        <el-button icon="el-icon-search" @click="getLists(1)" size="mini" type="primary">搜索</el-button>
                        <el-button @click="searchForm.keyword='',searchForm.send_date='',searchForm.expire_date='',getLists(1)" size="mini" type="text">取消搜索</el-button>
                    </div>
                </el-form>
                <el-table :data="tmpList.list" style="width: 100%;" size="mini" lable-width="180"
                          @selection-change="handleSelectionChange">
                    <el-table-column label="优惠券名称">
                        <template slot-scope="scope">
                            <div class="ellipsis-02">{{scope.row.name}}</div>
                        </template>
                    </el-table-column>

                    <el-table-column label="抵用金额" prop="money" ></el-table-column>

                    <el-table-column label="消费需满足" prop="condition" ></el-table-column>

                    <el-table-column label="获取类型" >
                        <template slot-scope="scope">
                            {{scope.row.type_mean}}
                        </template>
                    </el-table-column>

                    <el-table-column label="使用范围">
                        <template slot-scope="scope">
                            <p>{{scope.row.userarea_mean}}</p>
                            <p v-if="scope.row.userarea > 0">{{scope.row.cat_name}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="总数/领取/使用" >
                        <template slot-scope="scope">
                            <p>总数：{{scope.row.create_num}}</p>
                            <p>领取：{{scope.row.send_num}}</p>
                            <p>使用：{{scope.row.use_num}}</p>
                        </template>
                    </el-table-column>


                    <el-table-column label="是否上架" >
                        <template slot-scope="scope">
                            <el-switch :value="scope.row.status" :inactive-value="0" :active-value="1" @change="setOnline(scope,scope.row.status)"></el-switch>
                            &nbsp
                        </template>
                    </el-table-column>

                    <el-table-column label="领取时间" width="200">
                        <template slot-scope="scope">
                            <p>从：{{scope.row.send_start_time}}</p>
                            <p>至：{{scope.row.send_end_time}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="有效期" width="200">
                        <template slot-scope="scope">
                            <p>从：{{scope.row.expire_start_time}}</p>
                            <p>至：{{scope.row.expire_end_time}}</p>
                        </template>
                    </el-table-column>

                    <el-table-column label="操作" width="60">
                        <template slot-scope="scope">
                                <el-button @click="selectTmp(scope)" type="text">选择</el-button>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="list-btn">
                    <div class="rt">
                        <el-pagination @current-change="getIndex" :page-size="tmpList.limit" layout="total, prev, pager, next" :total="tmpList.count"></el-pagination>
                    </div>
                </div>
            </div>
        </el-dialog>
    </div>

</template>

<script>
    import * as api from "@/api.js";
    import DateBetween from "@/components/DateBetween";
    export default {
        name: "SmsTmpSelect",
        components: {DateBetween},
        data(){
            return {
                loading: false,
                dialogTableVisible: false,
                tmpList:[],
                searchForm: [],
                datas: {},
                useArea: [],

            }
        },
        created(){
            this.getIndex();
        },
        methods:{
            getIndex(page = 1){
                api.post("/shop/admin.marketing.coupon/getIndex", {
                    appToken: this.$store.getters.appToken,
                    page:page,
                }).then(res => {
                    this.tmpList = res;
                }).catch((error) => {

                });
            },



            selectTmp(val){
                this.dialogTableVisible = false;
                this.$emit('select-tmp', val.row);

            },


            handleSelectionChange(val) {
                console.log(val);
            },
        },

    }
</script>

<style scoped>

</style>
