<template>
    <div>
        <TopNav mdl="coupon" select="2"></TopNav>
        <div class="wyu-tips wyu-tips-warning">
            <p>1 ).这里是给用户赠送优惠券</p>
            <p>2 ).可用选择给全部用户赠送优惠券或指定部分用户赠送</p>
            <p>3 ).可选赠送优惠券的同时是否发送短信给用户</p>
            <p>4 ).单次操作赠送人数不得超过200人(群发短信云服务限制200个号码)</p>
        </div>

        <div class="wyu-main">
            <div class="wyu-block" v-loading="loading">
                <el-form ref="form" :model="form" label-width="100px" style="width: 800px">
                    <el-form-item label="用户列表">
                        <select-user-list @select-user="selectUser"></select-user-list>
                        <template>
                            <el-table size="mini" :data="users" height="250" border style="width: 100%">
                                <el-table-column prop="nickname" label="用户"></el-table-column>
                                <el-table-column prop="mobile" label="手机号"></el-table-column>
                                <el-table-column prop="address" label="操作" width="50">
                                    <template slot-scope="scope">
                                        <el-button type="text" @click="deleteUser(scope.$index)" size="small">删除
                                        </el-button>
                                    </template>
                                </el-table-column>
                            </el-table>
                        </template>
                        <p>当前人数 {{userNum}} 人</p>
                    </el-form-item>

                    <el-form-item label="选择优惠券" >
                        <coupon-select @select-tmp="selecrTmp"></coupon-select>
                        <el-row :gutter="24" style="background-color: #e5e9f2;">
                            <el-col :span="6">优惠券名称</el-col>
                            <el-col :span="4">抵用金额</el-col>
                            <el-col :span="4">需满金额</el-col>
                            <el-col :span="5">领取时间</el-col>
                            <el-col :span="5">使用时间</el-col>
                        </el-row>
                        <el-row :gutter="24">
                            <el-col :span="6"><div class="ellipsis-02">{{coupon.name}}</div></el-col>
                            <el-col :span="4"><div class="ellipsis-02">{{coupon.money}}</div></el-col>
                            <el-col :span="4"><div class="ellipsis-02">{{coupon.condition}}</div></el-col>
                            <el-col :span="5">
                                <div class="ellipsis-02">
                                    <p v-if="coupon.send_start_time">从：{{coupon.send_start_time}}</p>
                                    <p v-if="coupon.send_end_time">至：{{coupon.send_end_time}}</p>
                                </div>
                            </el-col>
                            <el-col :span="5">
                                <div class="ellipsis-02">
                                    <p v-if="coupon.expire_start_time">从：{{coupon.expire_start_time}}</p>
                                    <p v-if="coupon.expire_end_time">至：{{coupon.expire_end_time}}</p>
                                </div>
                            </el-col>
                        </el-row>
                    </el-form-item>

                    <el-form-item label="发送短信提醒">
                        <el-radio-group v-model="form.is_sms">
                            <el-radio :label="0">否</el-radio>
                            <el-radio :label="1">是</el-radio>
                        </el-radio-group>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">立即赠送</el-button>
                    </el-form-item>
                </el-form>
            </div>
        </div>

    </div>
</template>

<script>
    import TopNav from "@/components/TopNav";
    import * as api from "@/api";
    import SelectUserList from "./components/SelectUserList";
    import CouponSelect from "./components/CouponSelect";

    export default {
        name: 'applyList',
        components: {TopNav,CouponSelect, SelectUserList},
        data: function() {
            return {
                loading: false,
                form: {
                    tels: "",
                    params: [],
                },
                params:[],
                smsYvlan: '',
                tmp: {},
                users: [],
                userNum: 0,
                coupon: {},
            };
        },
        created() {
        },
        methods: {
            selecrTmp(val) {
                this.coupon = val;
            },

            onSubmit() {
                this.loading = true;
                var  userIds = [];
                this.users.forEach(res => {
                    userIds.push(res.user_id);
                });

                api.post("/shop/admin.marketing.coupon/sendCoupon", {
                    appToken: this.$store.getters.appToken,
                    userIds:userIds,
                    coupon_id: this.coupon.coupon_id,
                    params:this.params,
                }).then(res => {
                    that.$message({
                        message:'发送成功',
                        type:'success',
                    });
                    this.tels = [];
                    this.tmp = {};
                    this.users = [];
                    this.params = [];
                    this.loading = false;
                }).catch((error) => {
                    this.loading= false;
                });
            },

            deleteUser(index) {
                this.users.splice(index, 1);
                this.userNum = this.users.length;
            },

            selectUser(val) {
                if ((+this.users.length + +val.length) > 100) {
                    this.$message({
                        message: '您最多添加100个用户！',
                        type: 'error'
                    });
                    return false;
                }
                val.forEach(row => {
                    this.users.unshift(row);
                });
                let newUsers = [];
                this.users = this.users.reduce(function (item, next) {
                    newUsers[next.user_id] ? '' : newUsers[next.user_id] = true && item.push(next);
                    return item;
                }, []);
                this.userNum = this.users.length;
            },
        }
    }
</script>




<style scoped>

    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }

</style>
